import { OPEN_DIALOG, SEND_MESSAGE } from "./action-types"

const InitialState = {
  openedDialog: null,
  list: [
    {id: 1,
      name: 'Anton',
      messages: [{addressee: 'Anton', text: ""},{addressee: 'Anton', text: " "},{addressee: 'Me', text: ",      "},{addressee: 'Anton', text: "     )))"},]
      },
    {id: 2,
      name: 'Dimon',
      messages: []
      },
    {id: 3,
      name: 'Stas',
      messages: []
      },
    {id: 4,
      name: 'Evgen',
      messages: []
      }
  ]
}

const dialogsReducer = (state = InitialState, action) => {
  switch (action.type) {
    case SEND_MESSAGE:
     return {...state, openedDialog: {...state.openedDialog, messages: [...state.openedDialog.messages, {addressee: 'Me', text: action.data}]}}
    case OPEN_DIALOG:
      return {...state, openedDialog: state.list.find(dialog => dialog.id === action.data)}
    default: return state;
  }
}
export const sendMessageActionCreator = (text) => ({type: SEND_MESSAGE,data: text});
export const openDialogActionCreator = (id) => ({type: OPEN_DIALOG, data: id})

export default dialogsReducer;