import {connect} from "react-redux";
import {itsMeAction} from "../redux/profileReducer";

const isMeWrap = (props) => {
    props.itsMeAction(props.currentProfile.userId === props.authData.id)
    return <>{props.children}</>
}

const IsMeWrapConnect = connect(
    (state) => ({
        authData: state.auth.authData,
        currentProfile: state.profilePage.currentProfile
    }),
    { itsMeAction }
)(isMeWrap)

const IsMeComponent = (Component) => (props) =>{
    return (
        <IsMeWrapConnect>
            <Component {...props}/>
        </IsMeWrapConnect>
    )
}

export default (component) => {
    return IsMeComponent(component)
}