import React, {Suspense} from "react";
import { BrowserRouter, Route, Routes } from 'react-router-dom';
import {connect} from "react-redux";
import './App.css';
import "bootstrap-icons/font/bootstrap-icons.css";
import Users from './pages/users/users';
import Sidebar from './semantic/sidebar/sidebar';
import MainContainer from "./semantic/main/mainContainer";
import HeaderContainer from "./semantic/header/HeaderContainer";
import Login from "./pages/Login/login";
import {onLogoutThunk, setAuthDataThunk} from "./redux/authReducer";
import {setInitializeAppThunk} from "./redux/appReducer";

const DialogContainer = React.lazy(() => import('./pages/dialogs/dialog/dialog_container'));
const DialogsListContainer = React.lazy(() => import('./pages/dialogs/dialogsList_container'));

class App extends React.Component {
  componentDidMount() {
    this.props.setInitializeAppThunk()
  }

  render()
  {
   if(!this.props.initialized) return <h1></h1>;
    return (
        <BrowserRouter>
          <div className="App">
            <HeaderContainer/>
            <Sidebar/>
            <Routes>
              <Route path='/login' element={<Login/>}/>
              <Route path='profile/' element={<MainContainer/>}>
                <Route path=':userId' element={<MainContainer/>}/>
              </Route>
                <Route path='/dialogs' element={<Suspense fallback={<div>PADJIII</div>}><DialogsListContainer/></Suspense>}/>
              <Route path='/dialogs/1' element={<Suspense fallback={<div>PADJIII</div>}><DialogContainer/></Suspense>}/>
              <Route path='/users' element={<Users/>}/>
            </Routes>
          </div>
        </BrowserRouter>
    );
  }
}

export default connect(
    (state) =>{
      return {
        isAuth: state.auth.isAuth,
        authData: state.auth.authData,
        initialized: state.app.initialized
      }
    },
    { setInitializeAppThunk, onLogoutThunk }
)(App);
