import {
  ADD_POST,
  SET_CURRENT_PROFILE,
  ITS_ME,
  GET_PROFILE_STATUS,
  UPDATE_MY_STATUS
} from "./action-types";
import {getProfileData, profileStatusApi} from "../api/api";

const InitialState = {
  posts: [
    {author: '', text: "JOOPAJOPAJOPAJOPAJKOAJOPAJOPAJOPAJOP"},
    {author: '', text: "JOOPAJOPAJOPAJOPAJKOAJOPAJOPAJOPAJOP"},
    {author: '', text: "JOOPAJOPAJOPAJOPAJKOAJOPAJOPAJOPAJOP"},
    {author: '', text: "JOOPAJOPAJOPAJOPAJKOAJOPAJOPAJOPAJOP"},
  ],
  currentProfile: {},
  isMe: false,
  status: null
}

const profileReducer = (state = InitialState, action) => {
  switch(action.type) {
    case ADD_POST:
      if(action.data) {
        return {...state, posts:[{author: '', text: action.data},...state.posts]};
      }
      return state;
    case SET_CURRENT_PROFILE:
      return {...state, currentProfile: action.data};
    case ITS_ME: {
      return {...state, isMe: action.data};
    }
    case GET_PROFILE_STATUS: {
      return {...state, status: action.data};
    }
    case UPDATE_MY_STATUS: {
      return {...state, status: action.data}
    }
    default: 
        return state;
  }
}

export const addPostActionCreator = (text) => ({type: ADD_POST, data: text});
export const setCurrentProfile = (profile) => ({type: SET_CURRENT_PROFILE, data: profile});
export const itsMeAction = (value) => ({type: ITS_ME, data: value});
export const getProfileStatusAction = (status) => ({type: GET_PROFILE_STATUS, data: status})
export const updateMyStatusAction = (status) => ({type: UPDATE_MY_STATUS, data: status})

export const setCurrentProfileThunk = (props) => (dispatch) => {
  getProfileData(props).then((response) => {
    dispatch(setCurrentProfile(response.data));
  })
}

export const getProfileStatusThunk = (id) => (dispatch) => {
  profileStatusApi.getProfileStatus(id).then((response) => {
    dispatch(getProfileStatusAction(response.data))
  })
}

export const updateMyStatusThunk = (status) => (dispatch) => {
  profileStatusApi.updateMyStatus(status).then((response) => {
    dispatch(updateMyStatusAction(status))
  })
}

export default profileReducer;
