import {DELETE_AUTH_DATA, SET_AUTH_DATA} from './action-types'
import {getMyAuthData, onLogin, onLogout} from "../api/api";

let initialState = {
    isAuth: false,
    authData: {
        userId: null,
        email: null,
        login: null,
    }
}

const authReducer = (state = initialState, action) => {
    switch (action.type){
        case SET_AUTH_DATA:
        {
            return {isAuth: true, authData: {...action.data}};
        }
        case DELETE_AUTH_DATA: {
            console.log(action.data)
            return {isAuth: false, authData: action.data}
        }
        default:
            return state;
    }

}

export const setAuthData = (data) => ({type: SET_AUTH_DATA, data: data});
export const deleteAuthData = () => ({type: DELETE_AUTH_DATA, data: {
        userId: null,
        email: null,
        login: null,
    }
})

export const onLogoutThunk = () =>
    (dispatch) => {
        onLogout().then((response) => {
            if(response.data.resultCode === 0){
                dispatch(deleteAuthData())
            }
    })
}

export const onLoginThunk = ({email, password, rememberMe}) =>
    (dispatch) => {
        onLogin(email, password, rememberMe=true)
            .then((response) => {
                if(response.data.resultCode === 0) {
                    dispatch(setAuthDataThunk())
                } 
            })
    }
export const setAuthDataThunk = () =>
    (dispatch) => {
        return getMyAuthData().then((response) => {
            if(response.data.resultCode === 0){
                dispatch(setAuthData(response.data.data));
            }
        });
}

export default authReducer;