import React from 'react';
import UsersList from "./usersList";

class UsersListAJAX
    extends React.Component {
    componentDidMount() {
        this.props.getUsersThunk(this.props)
    }

componentDidUpdate(prevProps) {
        if(this.props.currentPage != prevProps.currentPage) {
            this.props.getUsersThunk(this.props)
        }
    }
    render() {
        return <UsersList list={this.props.list} isFetching={this.props.isFetching}/>
    }
}

export default UsersListAJAX
;