import './usersList.css'

const UsersList = (props) => {
    return (
        <>
            { (props.isFetching)
                ? <div className='preloader'><i/><i/><i/><i/><i/><i/><i/><i/></div>
                : <div className="users-list">{props.list}</div>
            }
        </>
    );
};

export default UsersList;