import React from "react";
import './users.css'
import UsersListContainer from "./usersList/usersList_container";
import UsersPaginationContainer from "./usersPagination/usersPagination_container";

const Users = () => {
  return (
    <div className="users">
      <h1>Users</h1>
      <UsersListContainer />
        <UsersPaginationContainer />
    </div>
  );
};

export default Users;