import React from 'react';
import './person.css'
import PersonStatus from "./PersonStatus/personStatus";

const Person = (props) => {
    const profile = props.currentProfile;
    return (
    <div className="person">
      <div className="person__ava-wrap">
          {profile?.photos && <img className='person__ava' src={profile.photos.large} alt=''/>}
      </div>
      <div className="person-data">
        <h1>{profile && profile.fullName}</h1>
            <PersonStatus status={props.status} isMe={props.isMe}/>
          {profile?.aboutMe && <p>{profile.aboutMe}</p>}
      </div>
    </div>
  )
};

export default Person;