import React from 'react';
import Container from '../../elements/container';
import './header.css'
import {Link} from "react-router-dom";

const Header = (props) => {
  return (
    <header>
      <Container>
        <h1>Hello</h1>
          {
              (props.isAuth)
                  ? <div><Link to='profile'>{props.authData.login}</Link><button onClick={props.onLogoutThunk}>Logout</button></div>
                : 'Login'
          }
      </Container>
    </header>
  )
}

export default Header;