import React from "react";
import {Form, Field} from 'react-final-form'
import {connect} from "react-redux";
import {onLoginThunk} from "../../redux/authReducer";
import {compose} from "redux";
import {requiredField} from "../../utils/validator";
import './login.css'
import WarningFormControl from "../../elements/FormControls/warningFormControl";
import redirectToProfileHOC from "../../HOCs/redirectToProfileHOC";
class Login extends React.Component{

    loginSubmit = (data) => {
        this.props.onLoginThunk(data)
    }
    render() {
        return (
            <div className='login'>
                <h1>Login</h1>
                <Form onSubmit={(data) => {
                    this.loginSubmit(data)
                }}
                >
                    {({handleSubmit, form}) => (
                        <form onSubmit={handleSubmit} name='login'>
                            <Field name='email'
                                   type='text'
                                   validate={requiredField}
                            >{props => (
                               <div className={'login__input-wrap'}>
                                   <input {...props.input}/>
                                   { props.meta.error &&
                                     props.meta.touched &&
                                     <WarningFormControl error={props.meta.error}/>
                                   }
                               </div>
                            )}</Field>

                            <Field name='password'
                                   type='password'
                            >{props => (
                                <div className={'login__input-wrap'}>
                                    <input {...props.input}/>
                                    { props.meta.error &&
                                        props.meta.touched &&
                                        <WarningFormControl error={props.meta.error}/>
                                    }
                                </div>
                            )}</Field>

                            <Field name='remember'
                                   component='input'
                                   type='checkbox'
                                   value='remember me'
                            />{''} Remember me

                            <button type={"submit"}>Login</button>
                        </form>
                    )}
                </Form>
            </div>
        );
    }
};

export default compose(
    connect(
        (state) => {
            return {}
        },
        {   onLoginThunk}
    ),
    redirectToProfileHOC
)(Login);