import React from 'react';
import './usersItem.css'
import {NavLink} from "react-router-dom";

const UsersItem = (props) => {
  return (
    <div className="users-item">
      <NavLink to={'/profile' + '/' + props.user.id} className="users-item__img-wrap">
        <img src={ (props.user.photos.large)
                    ? props.user.photos.large
                    : "https://upload.wikimedia.org/wikipedia/ru/3/3e/Mortician_%E2%80%94_Hacked_Up_for_Barbecue.jpg"
                 }
             className="users-item__img"
             alt={''}
        />
      </NavLink>
      <div className="users-item__foobar">
        <div className="users-item__info">
        <h3>{props.user.name}</h3>
        <p>{props.user.location}</p>
        </div>
        <button type='button' className="users-item__follow" onClick={props.onFollow}>
          <i>{(props.user.followed) ? 'unfollow' : 'follow'}</i>
        </button>
      </div>
    </div>
  );
};

export default UsersItem;