import React from "react";
import { connect } from "react-redux";
import UsersListAJAX from "./UsersListAJAX";
import UsersItemContainer from "./usersItem/usersItem_container";
import { setUsersActionCreator, setTotalCountActionCreator, toggleIsFetchingActionCreator, getUsersThunk } from "../../../redux/usersReducer";
import {getProfileStatusThunk} from "../../../redux/profileReducer";
import {getUsersHighSelector} from "../../../selectors/selector";

const mapStateToProps = (state) => {
  return {
    list: getUsersHighSelector(state),
    pageSize: state.users.pageSize,
    currentPage: state.users.currentPage,
    isFetching: state.users.isFetching
  }
},
UsersListContainer = connect(
    mapStateToProps,
    {
        setUsersActionCreator,
        setTotalCountActionCreator,
        toggleIsFetchingActionCreator,
        getUsersThunk,
        getProfileStatusThunk
    })(UsersListAJAX);

export default UsersListContainer;