import {connect} from "react-redux";
import MainContainerAJAX from "./mainContainer_AJAX";
import {getProfileStatusThunk, setCurrentProfileThunk, updateMyStatusThunk} from "../../redux/profileReducer";
import {Navigate, useParams} from "react-router-dom"
import LOGIN_REDIRECT_HOC from "../../HOCs/RedirectToLoginHOC";
import isMeHOC from '../..//HOCs/isMeHOC'
import {compose} from "redux";

const MainContainer = (props) => {
    const { userId } = useParams();
    return <MainContainerAJAX {...props} urlIdParam={userId}/>
}

const mapStateToProps = (state) => ({
    isMe: state.profilePage.isMe,
    currentProfile: state.profilePage.currentProfile,
    status: state.profilePage.status
})

export default compose(
    connect(mapStateToProps, { setCurrentProfileThunk, getProfileStatusThunk, updateMyStatusThunk }),
    isMeHOC
)(MainContainer)


