import {Navigate} from "react-router-dom";
import {connect} from "react-redux";
import {compose} from "redux";

const isAuthControl = (props) => {
    if(!props.isAuth) {
        return <Navigate to={'/login'}/>
    }
    return <>{props.children}</>
}
const IsAuthControlConnect = connect((state)=>({isAuth: state.auth.isAuth}))(isAuthControl);


const RedirectToLoginHoc = (Component) => (props) => {
    return (
        <IsAuthControlConnect>
            <Component {...props} />
        </IsAuthControlConnect>
    )
};

const loginRedirectHOC = (component) => {
    return RedirectToLoginHoc(component)
};
export default loginRedirectHOC