import React, {Component} from 'react';
import Main from "./main";
import Axios from "axios";

class MainContainerAJAX extends Component {
    componentDidMount() {
        if(this.props.urlIdParam){
            this.props.setCurrentProfileThunk(this.props.urlIdParam);
            this.props.getProfileStatusThunk(this.props.urlIdParam)
        }
    }

    componentDidUpdate(prevProps) {
        if(this.props.urlIdParam && this.props.urlIdParam !== prevProps.urlIdParam) {
            this.props.setCurrentProfileThunk(this.props.urlIdParam);
        }
    }

    render() {
        return <Main {...this.props} />
    }
}

export default MainContainerAJAX;