import './main.css'
import MyPostsContainer from './MyPosts/myPosts_container';
import Person from './Person/person';
import {useMatch} from "react-router";

const Main = (props) => {
  return (
    <main className='main'>
      <div className="main__head">
        <img className="main__head-img" src='https://place-hold.it/700x200' alt=''/>
      </div>
      <Person {...props}/>
      <MyPostsContainer />
    </main>
  );
};

export default Main;