import React from 'react';
import Avatar from '../../../../elements/avatar';
import './post.css'

const Post = (props) => {
  return (
    <div className="post">
      <Avatar />
      <div className="post__body">
        <h2 className="post__author">{props.author}</h2>
        <p className="post__text">{props.text}</p>
      </div>
    </div>
  );
};

export default Post;