import React from 'react';
import {Form, Field} from "react-final-form";
import Buttons from "../../../../elements/buttons";

const AddNewPost = (props) => {
    return (
        <Form onSubmit={(data)=>{props.onSubmit(data.postText)}}
              initialValues = {
                  {"postText": ''}
              }
        >
            {({handleSubmit}) => (
                <form onSubmit={handleSubmit}>
                    <Field id="new-post"
                           component={'textarea'}
                           name={'postText'}
                    />
                    <Buttons title="Add" callback={handleSubmit} />
                </form>
            )}
        </Form>
    );
};

export default AddNewPost;