import React from "react";
import {createSelector} from "reselect";
import UsersItemContainer from "../pages/users/usersList/usersItem/usersItem_container";

export const getUsersSelector = (state) => {
    return state.users.list;
}

export const getUsersHighSelector = createSelector(getUsersSelector, (usersList) => {
    return usersList.map((user) => {
        return (<UsersItemContainer
            user={user}
            key={user.id}
        />)
    })
})