import {INITIALIZED} from "./action-types";
import {setAuthDataThunk} from "./authReducer";

let initialState = {
    initialized: false
}

const appReducer = (state = initialState, action) => {
    switch(action.type) {
        case INITIALIZED:
        {
            return {...state, initialized: action.value}
        }
        default: return state;
    }
}

const initializedAction = (value) => ({type: INITIALIZED, value: value})

export const setInitializeAppThunk = () => (dispatch) => {
    let dispatchResult = dispatch(setAuthDataThunk());
    Promise.all([dispatchResult]).then(()=>{
        dispatch(initializedAction(true))
    })
}

export default appReducer;