import {Navigate} from "react-router-dom";
import {connect} from "react-redux";

const IsAuthControl = (props) => {
    if(props.isAuth) {
        return <Navigate to={`/profile/${props.id}`}/>
    }
    return <>{props.children}</>
}
const IsAuthControlConnect = connect(
    (state) => {
        return {
            isAuth: state.auth.isAuth,
            id: state.auth.authData.userId
        }
    }
)(IsAuthControl);
const isAuthComponent = (Component) => (props) => {
    return (
        <IsAuthControlConnect>
            <Component {...props}/>
        </IsAuthControlConnect>
    )
}
export default (component) => {
    return isAuthComponent(component)
}