import Axios from 'axios';

const url = 'https://social-network.samuraijs.com/api/1.0';
const params = {

}

const instance = Axios.create({
    withCredentials: true,
    baseURL: url,
    headers: {
        "API-KEY": '1647e97d-622d-47cd-85b2-9571ffbe9f86'
    }
})

export const getUsers = (props) => {
    return instance.get(`/users?page=${props.currentPage}&count=${props.pageSize}`)
}

export const getMyAuthData = () => {
    return instance.get(`/auth/me`)
}

export const getProfileData = (id) => {
    return instance.get(`/profile/${id}`)
}

export const profileStatusApi = {
    getProfileStatus: (id) => {
        return instance.get(`/profile/status/${id}`)
    },
    updateMyStatus: (status) => {
        return instance.put(`/profile/status`, {status: status})
    }
}

export const onLogin = (email, password, rememberMe) => {
    return instance.post(`/auth/login`, {
        email: email,
        password: password,
        rememberMe: rememberMe
    })
}

export const onLogout = (email, password, rememberMe) => {
    return instance.delete(`/auth/login`)
}