import {FOLLOW, SET_USERS, CHANGE_PAGE, SET_TOTAL_COUNT, TOGGLE_IS_FETCHING} from "./action-types";
import {getUsers} from "../api/api";

const InitialState =  {
    list: [],
    pageSize: 10,
    currentPage: 1,
    isFetching: true,
}

const usersReducer = (state = InitialState, action) => {
    console.log(action.type)
  switch (action.type) {
    case FOLLOW:
      return {
          ...state,
          list: state.list.map(user => {
            return (user.id === action.data) ? {...user, followed: !user.followed} : user;
          })
      };
    case SET_USERS:
      return {...state, list: action.data};
    case CHANGE_PAGE:
      return {...state, currentPage: action.data};
    case SET_TOTAL_COUNT:
        return {...state, totalCount: action.data};
      case TOGGLE_IS_FETCHING:
          return {...state, isFetching: action.data};
    default: 
      return state;
  }
}

export const followActionCreator = (user) => ({type: FOLLOW, data: user});
export const setUsersActionCreator = (users) => ({type: SET_USERS, data: users});
export const changePageActionCreator = (number) => ({type: CHANGE_PAGE, data: number});
export const setTotalCountActionCreator = (number) => ({type: SET_TOTAL_COUNT, data: number});
export const toggleIsFetchingActionCreator = (value) => ({type: TOGGLE_IS_FETCHING, data: value});

//thuncks
export const getUsersThunk = (props) =>
    (dispatch) => {
        dispatch(toggleIsFetchingActionCreator(true))
        getUsers(props).then((response) => {
            dispatch(setUsersActionCreator(response.data.items));
            dispatch(setTotalCountActionCreator(response.data.totalCount));
            dispatch(toggleIsFetchingActionCreator(false));
        });
}

export default usersReducer;
