import React from "react";

class PersonStatus extends React.Component{
    state = {
        editMode: false,
        status: this.props.status
    }
    editModeOn = () => {
        this.setState({
            editMode: true
        })
    }
    editModeOff = () => {
        this.setState({
            editMode: false,
            status: this.props.status
        })
    }
    onTextChange = (e) => {
        this.setState({
            status: e.currentTarget.value
        })
    }
    updateMyStatus = (text) => {
        console.log('aaaaa')
        this.props.updateStatus(text);
    }
    render() {
        return (
            <div>
                { (this.state.editMode)
                    ? <form onSubmit={() => {this.updateMyStatus(this.props.status)}} onBlur={this.editModeOff}>
                        <input value={this.state.status}
                               onChange={(e) => {
                                   this.onTextChange(e)
                               }}
                               autoFocus={true}/>
                        <button type='submit'></button>
                      </form>

                    : <p>
                        {(this.props.status) ? this.props.status : this.props.isMe && <span>  </span>}
                        {this.props.isMe && <i className="bi bi-pencil-fill" onClick={this.editModeOn}></i>}
                      </p>
                }
            </div>
        );
    }
};

export default PersonStatus;